.navbar {
  display: grid;
  height: 40px;
  padding: 4px;
}
.left {
  width: 100%;
  grid-column: 1;
  text-align: left;
}
.center {
  width: 100%;
  grid-column: 2;
  text-align: center;
}
.right {
  width: 100%;
  grid-column: 3;
  text-align: right;
}



