.diarioentry {
  color: #7dff7d;
  border-left: 3px solid #7dff7d;
  background-color: rgba(125, 255, 125, 0.1);
  padding: 4px 4px 4px 8px;
  margin: 8px;
  display: grid;
  grid-row-gap: 4px;
  grid-column-gap: 4px;
}
.topleft {
  grid-column: 1;
  grid-row: 1;
  align-self: start;
  text-align: left;
}
.bottomleft {
  grid-column: 1;
  grid-row: 2;
  align-self: end;
  text-align: left;
}
.topright {
  grid-column: 2;
  grid-row: 1;
  align-self: start;
  text-align: right;
}
.bottomright {
  grid-column: 2;
  grid-row: 2;
  align-self: end;
  text-align: right;
}
.info {
  font-size: smaller;
}
.save {
  font-size: smaller;
}



